import { Container, Graphics, Text } from "@pixi/react";
import React, { useCallback, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { ActionTypes } from "../../types";
import { TextStyle } from 'pixi.js';
import { Coin } from './coin';
import { Counter } from "./counter";
import { DropShadowFilter } from "@pixi/filter-drop-shadow";
import { InfoText } from './infoText';
const bets = [1, 5, 10, 25, 100, 500];
const selectedFilter = new DropShadowFilter();
selectedFilter.color = 0xffffff;
selectedFilter.quality = 10;
selectedFilter.distance = 3;
const step = 35;
export const offset = 70;
const defaultBet = 5;
// SizeProps & 
export const BetSelector = ({ seconds }) => {
    const balance = useSelector((state) => state.player.total_balance);
    const [title, setTitle] = useState('Place your bets');
    const [selectedBet, setSelectedBet] = useState(5);
    const [doubled, setDoubled] = useState(0);
    const [totalBet, setTotalBet] = useState(defaultBet);
    const [error, setError] = useState('');
    const dispatch = useDispatch();
    const calcResultValue = (selectedBetValue, doubledByTimes) => {
        return doubledByTimes > 0 ? selectedBetValue * (doubledByTimes * 2) : selectedBetValue;
    };
    const onSelectBetHandler = (betValue) => {
        if (betValue !== selectedBet && checkIfBetValid(betValue, 0)) {
            setSelectedBet(betValue);
            resetDoubled();
            setTotalBet(betValue);
        }
    };
    const handleOnDouble = () => {
        if (checkIfBetValid(selectedBet, doubled + 1)) {
            const resValue = calcResultValue(selectedBet, doubled + 1);
            if (bets.includes(resValue)) {
                setSelectedBet(resValue);
            }
            else {
                setDoubled(prev => prev + 1);
            }
            setTotalBet(resValue);
        }
    };
    const handleUndo = () => {
        setSelectedBet(defaultBet);
        resetDoubled();
        setTotalBet(defaultBet);
        checkIfBetValid(defaultBet, doubled);
    };
    const resetDoubled = () => {
        setDoubled(0);
        setTotalBet(selectedBet);
        checkIfBetValid(defaultBet, doubled);
    };
    const checkIfBetValid = (betV, doubledV) => {
        const isValid = calcResultValue(betV, doubledV) <= balance;
        if (isValid) {
            setError('');
        }
        else {
            setError('Unsufficient balance for bet');
        }
        return isValid;
    };
    const handleBetsClosing = () => {
        setTitle('bets are closing');
    };
    const handleTimeUp = () => {
        setTitle('bets closed');
        dispatch({
            type: ActionTypes.PLACE_BET,
            payload: {
                value: totalBet
            }
        });
    };
    const coinPositions = [
        { x: -step * 5, y: 0 },
        { x: -step * 3, y: 0 },
        { x: -step, y: 0 },
        { x: step, y: 0 },
        { x: step * 3, y: 0 },
        { x: step * 5, y: 0 },
    ];
    return (React.createElement(Container, { anchor: 0.5 },
        React.createElement(InfoText, { position: { x: 0, y: -offset }, title: title }),
        bets.map((betValue, index) => {
            const isSelected = selectedBet === betValue;
            const coin = (React.createElement(Coin, { key: `${betValue}`, bet: betValue, isSelected: selectedBet === betValue, onSelect: onSelectBetHandler, position: coinPositions[index] }));
            if (isSelected) {
                return (React.createElement(Container, { filters: [selectedFilter], key: `${betValue}` }, coin));
            }
            return coin;
        }),
        React.createElement(Container, { anchor: 0.5, position: { x: 0, y: offset } },
            React.createElement(Button, { text: "UNDO", onClick: handleUndo, color: 0x808080, width: 80, height: 30, position: { x: -50, y: 0 } }),
            React.createElement(Button, { text: "DOUBLE", onClick: handleOnDouble, color: 0x00000, width: 80, height: 30, position: { x: 50, y: 0 } })),
        React.createElement(Counter, { position: { x: 0, y: offset * 2 }, seconds: seconds, onTimeUp: handleTimeUp, preTimeUpSeconds: 5, onPretimeUp: handleBetsClosing }),
        React.createElement(InfoText, { title: `total bet: ${totalBet}`, position: { x: 0, y: offset * 3 }, fontSize: 20 }),
        React.createElement(InfoText, { title: error, position: { x: 0, y: offset * 3.5 }, fontSize: 20 })));
};
const Button = ({ onClick, width, height, color, text, position }) => {
    const draw = useCallback((g) => {
        g.clear();
        g.beginFill(color);
        g.drawRoundedRect(0 - width / 2, 0 - height / 2, width, height, 5);
        g.endFill();
    }, []);
    return (React.createElement(Container, { pointerdown: onClick, interactive: true, position: position },
        React.createElement(Graphics, { draw: draw }),
        React.createElement(Text, { text: text, style: new TextStyle({
                align: 'center',
                fontFamily: '"Source Sans Pro", Helvetica, sans-serif',
                fill: ['#ffffff'],
                fontSize: 14
            }), anchor: 0.5 })));
};
